<template>
    <yd-layout>
        <div class="list">
            <div class="listOne" v-for="(item, index) in list" :key="index">
                <div class="top">
                    <div class="title" v-text="item.title"></div>
                    <div class="time" v-text="formatDate(item.versionTime,'yyyy-MM-dd')"></div>
                </div>
                <div class="text" v-html="item.html"></div>
            </div>
        </div>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
    name: 'versionHistory',
    data() {
        return {
            list: []
        }
    },
    mounted() {
        // 8月16号上线
        document.setTitle('版本历史记录');
        let vm = this;
        vm.versionList();
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo'
        })
    },
    methods: {
        versionList() {
            let vm = this;
            vm.get(vm.conf.api + '/AddressBook/notice/versionList', {
                'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`
            }, function (res) {
                vm.list = res.versionList;
            });
        }
    }
}
</script>
<style lang="less" scoped>
.list {
    background-color: #fff;
    .listOne {
        min-height: 2.78rem;
        border-bottom: 1px solid #eaeaea;
        padding: .4rem .42rem;
        .top {
            display: flex;
            justify-content: space-between;
        }
        .time {
            font-size: .24rem;
            color: #999;
        }
        .title {
            font-size: .3rem;
            font-weight: bold;
        }
        .text {
            font-size: .28rem!important;
            line-height: .5rem;
            margin-top: .3rem;
            word-break: break-all;
        }
    }
    .listOne:last-child {
        border-bottom: none;
    }
}
</style>


